import React from 'react';
import { alphabetical } from '../../util.module.js';
import '../../util.scss';
import './type-layout.scss';

export default class TypeLayout extends React.Component {

	DamageRelationElement = () => {
		const dmgRelations = this.props.type.damage_relations; 
		const keys = Object.keys(dmgRelations).filter(
			key => dmgRelations[key].length
		);
		for (const key of keys) {
			dmgRelations[key].sort((a,b) => alphabetical(a.name, b.name));
		}
		return (
			<div className="flex types">
			{
				keys.map(key => {
					return ( 
						<div className="flex-column damage-relation" key={key}>
							<div className="title">{key.replace(/_/g, " ")}</div>
							<div className="flex-column">
							{
								dmgRelations[key].map(type => {
									const name = type.name;
									return (
										<button 
											className={name} 
											key={name}
											onClick={() => this.props.onSelection(name)}>
												{name}
											</button>
									);
								})
							}
							</div>
						</div>
					);
				})
			}
			</div>
		);
	}

	DamageClassElement = () => {
		const name = this.props.type.name;
		let dmgclass; 
		if (this.props.type.move_damage_class) {
			dmgclass = this.props.type.move_damage_class.name
		}
		return (
			<div className={"flex-column dmg-class " + name}>
				<div className="type-title">{name}</div>
				<div className="dmg-class-name">{dmgclass}</div>
			</div>
		);
	}
	render() {
		return (
			<div className="flex type-layout">
				{this.DamageClassElement()}
				{this.DamageRelationElement()}
			</div>
		);
	}
}