import React from 'react';
import { P, alphabetical, round, LoaderElement, get, byGameRelease, 
	gamePairs, consideredLocations } from '../../util.module.js';
import { Modal } from 'semantic-ui-react';

import '../../util.scss';
import './pokemon-layout.scss';

export default class PokemonLayout extends React.Component {

	// Mounting Lifecycles ---------------------------------------------

	constructor(props) {
		super(props);
		this.state = {
			pokemon: props.pokemon,
			error: false,
			initializing: false,
			locationEncounters: [],
			species: null,
			abilities: [],
			heldItems: [],
			evolutionMap: [],
		};
		this.updateAll();
	}

	static getDerivedStateFromProps(nextProps, prevState) {
		if (nextProps.pokemon.id !== prevState.pokemon.id) {
			return {
	 			pokemon: nextProps.pokemon,
			  error: false,
			  initializing: false,
			  locationEncounters: [],
			  species: null,
			  abilities: [],
			  heldItems: [],
			  moves: [],
			  evolutionMap: [],
			};
		}
		return null;
	}
	// Methods ---------------------------------------------------------

	updateLocationEncounters = () => {
		get(this.props.pokemon.location_area_encounters, response => {
			this.setState({locationEncounters: response});
		});
	}

	updateSpecies = () => {
		get(this.props.pokemon.species.url, response => {
			this.setState({species: response});
			get(response.evolution_chain.url, response => {
				let queue = [response.chain];
				let element = [];
				while (queue.length) {
					const curr = queue.shift();
					element.push(curr.species.name);
					curr.evolves_to.forEach(chain => queue.push(chain));
				}
				for (let i = 0; i < element.length; i++) {
					P.getPokemonByName(element[i], (response,error) => {
						if (!error) {
							const evolutionMap = this.state.evolutionMap.slice();
							evolutionMap.push([i,response]);
							this.setState({evolutionMap});
						} else {
							console.error(error);
						}
					});
				}
			});
		});
	}

	updateAbilities = () => {
		this.props.pokemon.abilities.forEach(ability => {
			get(ability.ability.url, response => {
				const abilities = this.state.abilities.slice(); 
				abilities.push(response);
				this.setState({abilities});
			})
		});
	}

	updateHeldItems = () => {
		this.props.pokemon.held_items.forEach(heldItem => {
			get(heldItem.item.url, response => {
				const heldItems = this.state.heldItems.slice(); 
				heldItems.push(response);
				this.setState({heldItems});
			});
		});
	}

	updateMoves = () => {
		this.props.pokemon.moves.forEach(move => {
			get(move.move.url, response => {
					const moves = this.state.moves.slice(); 
					moves.push(response);
					this.setState({moves});
			});
		});
	}

	updateAll = () => {
		this.updateLocationEncounters();
		this.updateSpecies();
		this.updateAbilities();
		this.updateHeldItems();
	}

	// Functional Components -------------------------------------------

	AbilitiesElement = () => {
		if (!this.state.abilities || !this.state.abilities.length) {
			return null; 
		}
		return (
			<div className="pokemon-container flex-column">
				<div className="bold">Abilities</div>
				<div className="abilities flex">
					{
						this.state.abilities.map(ability  => {
							let effect;
							if (ability.effect_entries.length) {
								effect = (
									<div className="text-align-center">
										{ability.effect_entries[0].effect}
									 </div>
								);
							}
							return (
								<Modal trigger={<button>{ability.name}</button>} key={ability.name}>
							    <Modal.Content>
							      <Modal.Description>
							      {effect}
							      </Modal.Description>
							    </Modal.Content>
							  </Modal>
							);
						})
					}
				</div>
			</div>
		);
	}

	BaseExpElement = () => {
		return (
			<div className="pokemon-container">
				<div className="label">Base EXP</div>
				<div>
					{this.state.pokemon.base_experience + " EXP"}
				</div>
			</div>
		);
	}

	HeightElement = () => {
		const height = (this.state.pokemon.height * 10)/30.48;
		return (
			<div className="pokemon-container height">
				<div className="label">Height</div>
				<div style={{'textTransform':'lowercase'}}>
					{round(height,2) + ' ft'}
				</div>
			</div>
		);
	}

	BaseHappinessElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Base Happiness</div>
				<div>
					{this.state.species.base_happiness} / 255
				</div>
			</div>
		);
	}

	CaptureRateElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Capture Rate</div>
				<div>
					{this.state.species.capture_rate} / 255
				</div>
			</div>
		);
	}

	GenerationElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Generation</div>
				<div style={{"textTransform":"uppercase"}}>
					{this.state.species.generation.name.split("-")[1]}
				</div>
			</div>
		);
	}

	GrowthElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Growth Rate</div>
				<div>
					{this.state.species.growth_rate.name}
				</div>
			</div>
		);
	}

	HabitatElement = () => {
		if (!this.state.species || !this.state.species.habitat || 
			  !this.state.species.habitat.name) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Habitat</div>
				<div>
					{this.state.species.habitat.name}
				</div>
			</div>
		);
	}

	HatchElement = () => {
		if (!this.state.species) {
			return null;
		}
		return (
			<div className="pokemon-container">
				<div className="label">Hatch Counter</div>
				<div>
					{Number(255*(this.state.species.hatch_counter + 1)).toLocaleString()} Steps
				</div>
			</div>
		);
	}

	NameElement = () => {
		if (!this.state.species) {
			return null;
		}
		let id = this.state.species.id.toString(10);
		while (id.length < 3) {
			id = "0" + id; 
		}
		id = "#" + id; 
		return (
			<div className="name-container">
				<div className="label">{this.state.pokemon.name}</div>
				<div className="id">{id}</div>
			</div>
		);		
	}

	MovesElement = () => {
		const moves = this.state.moves;
		moves.sort((a,b) => alphabetical(a.name, b.name));
		return (
			<div className="pokemon-container flex-column">
				<div className="label">Moves</div>
				<div className="moves">
					{
						moves.map(move => {
							let effect; 
							if (move.effect_entries.length) {
								effect = move.effect_entries[0].effect;
							}
							return (
								<Modal trigger={<button>{move.name}</button>} key={move.name}>
							    <Modal.Content>
							      <Modal.Description>
								      <div className="flex-column">
								      	<div className="flex">
								      		<div>Accuracy</div>
								      		<div>{move.accuracy}</div>
								      	</div>
								      	<div className="flex">
								      		<div>Type</div>
								      		<div>{move.type.name}</div>
								      	</div>
								     		<div className="flex">
								      		<div>Power</div>
								      		<div>{move.power}</div>
								      	</div>
								      	<div className="flex">
								      		<div>pp</div>
								      		<div>{move.pp}</div>
								      	</div>
								      	{effect}
								      </div>
							      </Modal.Description>
							    </Modal.Content>
							  </Modal>
							);
						})
					}
				</div>
			</div>
		);
	}

	FlavorTextElement = () => {
		if (!this.state.species || !this.state.species.flavor_text_entries ||
			  !this.state.species.flavor_text_entries.length) {
			return null; 
		}
		const englishOnly = new Map(this.state.species.flavor_text_entries
			.filter(entry => entry.language.name === "en")
			.map(entry => [entry.version.name, entry.flavor_text]));
		for (let [game1, game2] of gamePairs) {
			game1 = game1.toLowerCase();
			game2 = game2.toLowerCase();
			if (englishOnly.has(game1) && englishOnly.has(game2)) {
				const text1 = englishOnly.get(game1);
				const text2 = englishOnly.get(game2);
				if (text1 === text2) {
					englishOnly.delete(game1);
					englishOnly.delete(game2);
					englishOnly.set(game1 + " & " + game2, text1);
				}
			}
		}
		const entries = Array.from(englishOnly).sort((a,b) => byGameRelease(a[0],b[0]));
		return (
			<div className="flex-column flavor-text">
			 	<div className="title">Pok√©dex Entry</div>
				{
					entries.map(([name, entry]) => {
						return (
							<div className="flex" key={name}>
								<div className="label">{name.replace(/-/g, " ")}</div>
								<div className='text'>{entry.replace(""," ")}</div>
							</div>
						);
				  })
				}
			</div>
		);
	}

	SpritesElement = () => {
		const sprites = Object.entries(this.state.pokemon.sprites)
													.filter(([name,url]) => url != null);
		sprites.sort((a,b) => alphabetical(a[0],b[0]));
		if (sprites.length === 0) {
			return null;
		}
		return (
			<div className="pokemon-container flex sprites-element">
				{
					sprites.map(([name,url]) => 
						{
							return (
								<div className="flex-column" key={name}>
									<img src={url} alt={name}/>
								</div>
							);
						}
					)
				}
			</div>
		);
	}

	StatsElement = () => {
		const stats = this.state.pokemon.stats;
		stats.sort((a,b) => alphabetical(a.stat.name, b.stat.name));
		return (
			<div className="pokemon-container flex-column">
				<table className="stat">
					<thead>
						<tr>
							<th>Stat</th>
							<th>Base</th>
							<th>Effort</th>
						</tr>
					</thead>
					<tbody>
					{
						stats.map(stat => 
							{ 
								return (
									<tr key={stat.stat.name}>
										<td>{stat.stat.name}</td>
										<td>{stat.base_stat}</td>
										<td>{stat.effort}</td>
									</tr>
								);
							}
						)
					}
					</tbody>
			  </table>
			</div>
		);
	}

	TypesElement = () => {
		const names = this.state.pokemon.types.map(type => type.type.name);
		names.sort();
		return (
			<div className="type-container">
				<div className="flex types">
					{
						names.map(name => 
							<button key={name} className={name} onClick={() => this.props.onSelection(name)}>{name}</button>
						)
					}
				</div>
			</div>
		);
	}

	WeightElement = () => {
		return (
			<div className="pokemon-container weight">
				<div className="label">Weight</div>
				<div style={{"textTransform": "lowercase"}}>
					{round(this.state.pokemon.weight / 4.536, 2) + " lbs"}
				</div>
			</div>
		);
	}

	HeldItemsElement = () => {
		if (!this.state.heldItems.length) {
			return null;
		}
		const items = this.state.heldItems;
		items.sort((a,b) => alphabetical(a.name, b.name));
		return (
			<div className="pokemon-container flex-column special">
				<div className="bold">Held Items (Wild)</div>
				<div className="flex held-items">
					{
						items.map(item => {
							let effect; 
							if (item.effect_entries.length) {
								effect = item.effect_entries[0].effect;
							}
							let source; 
							if (item.sprites && item.sprites.default) {
								source = item.sprites.default;
							}
							return (
								<Modal trigger={<button>{item.name}</button>} key={item.name}>
							    <Modal.Content>
							      <Modal.Description>
							      	<div className="flex">
												<img src={source} alt={item.name}/>
												<div className="held-effect">{effect}</div>
											</div>
							      </Modal.Description>
							    </Modal.Content>
							  </Modal>
						  );
						})
					}
				</div>
			</div>
		);
	}

	LocationElement = () => {
		if (!this.state.locationEncounters.length) {
			return null;
		}
		const gameToLocations = new Map(); 
		this.state.locationEncounters.forEach(locationEncounter => {
			const location = locationEncounter.location_area.name;
			locationEncounter.version_details.forEach(version => {
				const name = version.version.name;
				if (consideredLocations.includes(name)) {
					if (gameToLocations.has(name)) {
						gameToLocations.get(name).push({version,location});
					} else {
						gameToLocations.set(name, [{version,location}]);
					}
				}
			});
		});
		const games = new Array(...gameToLocations);
		games.sort((a,b) => byGameRelease(a[0],b[0]));
		for (let i = 0; i < games.length; i++) {
			if (gamePairs.has(games[i][0])) {
				games[i][0] += " & " + gamePairs.get(games[i][0]);
			}
		}
		return (
			<div className="pokemon-container flex-column">
			  <div className="title">Where to find in game</div>
				<div className="flex-column games">
			  {
			  	games.map(([name, locations]) => {
			  		return (
			  			<div className="flex game" key={name}>
			  				<div className="label-container">
			  					<div className="label">{name.replace(/-/g, " ")}</div>
			  				</div>
			  				<div className="flex region-container">
			  				{
			  					locations.map(location => {
			  						const region = location.location;
			  						const version = location.version;
			  						return (
			  							<div className="flex-column location" key={region}>
			  								<div className="region-name label">{region.replace(/-/g, " ")}</div>
		  									<div className="flex encounters">
			  									{
			  										version.encounter_details.map((encounter, i) => {
			  											let conditionElement; 
			  											if (encounter.condition_values.length) {
			  												conditionElement = (
			  													<div className="flex">
			  														<div className="label">Condition</div>
			  														<div>{encounter.condition_values.map(condition => condition.name).join(", ").replace(/-/g, " ")}</div>
			  													</div> 
			  												);
			  											}
			  											let level = (
		  													<div className="flex">
		  														<div className="label">Level</div>
		  														<div>{encounter.max_level}</div>
		  													</div>
			  											); 
			  											if (encounter.max_level !== encounter.min_level) {
			  												level = (
			  													<React.Fragment>
																		<div className="flex">
				  														<div className="label">Max LVL</div>
				  														<div>{encounter.max_level}</div>
				  													</div>
				  													<div className="flex">
				  														<div className="label">Min LVL</div>
				  														<div>{encounter.min_level}</div>
				  													</div>
				  												</React.Fragment>
			  												);
			  											}
			  											return (
			  												<div className="encounter" key={i}>
			  													<div className="flex">
			  														<div className="label">Method</div>
			  														<div>{encounter.method.name.replace(/-/g, " ")}</div>
			  													</div>
			  													<div className="flex">
			  														<div className="label">Chance</div>
			  														<div>{encounter.chance}%</div>
			  													</div>
			  													{level}
			  													{conditionElement}
			  												</div>
			  											);
			  										})
			  									}
			  								</div>
			  							</div>
			  						);
			  					})
			  				}
			  				</div>
			  			</div>
			  		);
			  	})
			  }
				</div>
			</div>
		);
	}


		// use beatifly as test case
	EvolutionElement = () => {
		if (!this.state.evolutionMap.length) {
			return null; 
		}
		const pokemons = this.state.evolutionMap
			.sort((a,b) => a[0] - b[0])
			.map(([order, pokemon]) => pokemon);
		return (
			<div className="flex-column evolution-chain">
				<div className="label">Evolution Chain</div>
				<div className="flex pokemon-evolutions">
				{
					pokemons.map(pokemon => {
						let id = pokemon.id.toString(10);
						for (let i = 0; i < 3 - id.length; i++) {
							id = "0" + id; 
						}
						id = "#" + id;
						return (
							<div 
								className="flex-column pokemon" 
								key={pokemon.id} 
								onClick={() => this.props.onSelection(pokemon.name)}>
								<img 
									src={pokemon.sprites.front_default} 
									alt={pokemon.name} />
								<div className="id">{id}</div>
								<div className="name">{pokemon.name}</div>
								<div className="flex button-container">
								{
									pokemon.types.map(type => {
										const name = type.type.name;
										return (
											<button className={name} key={name} onClick={() => this.props.onSelection(name)}>{name}</button>
										);
									})
								}
								</div>
							</div>
						);
					})
				}
				</div>
			</div>
		);
	}

	PokemonProfileElement = (name) => {
		P.get()
	}

	render() {
		if (!this.state.pokemon) {
			return null;
		} else if (this.state.pokemon && this.state.initializing) {
			return LoaderElement(); 
		}
		return (
			<div className="pokemon-layout flex-column">
				<div className="flex">
					<div className="basic-info">
						{this.NameElement()}
						{this.TypesElement()}
						{this.HeightElement()}
						{this.WeightElement()}
					</div>
					{this.SpritesElement()}
				</div>
				<div className="flex metadata">
					{this.StatsElement()}
					<div className="ability-move flex-column">
					  <div className="flex dialogs">
							{this.AbilitiesElement()}
							{this.HeldItemsElement()}
						</div>
					</div>
					<div className="metadata-2">
						{this.BaseExpElement()}
						{this.BaseHappinessElement()}
						{this.CaptureRateElement()}
						{this.GenerationElement()}
						{this.GrowthElement()}
						{this.HabitatElement()}
						{this.HatchElement()}
					 </div>
				</div>
				{this.EvolutionElement()}
				{this.FlavorTextElement()}
				{this.LocationElement()}
			</div>
		);
	}

	componentDidUpdate(prevProps) {
		if (prevProps.pokemon.id !== this.props.pokemon.id) {
			this.updateAll();
		}
	}
}